// colors
[data-dark="false"] {
  --primary: #116fca; /* All the buttons under highlights etc */
  --secondary: #14453d; /* Color change on hover */
  --text: #01053d; /* Black text */
  --headercolor: #042a2b; 
  --footercolor:#042a2b;
  --background: #ffffff; /* White background */
  --background-alt: #ffffff; /* Light gray for alternate backgrounds */
  --light-gray: #e0e0e0; /* Light gray for borders and subtle elements */
  --gray: #b0b0b0; /* Medium gray for less prominent elements */
  --dark-gray: #404040; /* Dark gray for accents or text in secondary areas */
 /* Light overlay for soft effects */
}
[data-dark="true"] {
  --primary: #0795d9;
  --secondary: #075985;
  --text: #ffffff;
  --background: #181818;
  --background-alt: #1c1c1c;
  --light-gray: #404040;
  --gray: #808080;
  --dark-gray: #b0b0b0;
 
}

:root {
  // font families
 //--title: "Barlow", sans-serif;
  //--heading: "Barlow", sans-serif;
  //--body: "Barlow", sans-serif;
  //--code: "Roboto Mono", monospace;

  --title: "Montserrat", sans-serif;
  --heading: "Montserrat", sans-serif;
  --body: "Source Sans Pro", sans-serif;
  --code: "Fira Code", monospace;

  // font sizes
  --large: 1.4rem;
  --xl: 1.6rem;
  --xxl: 1.8rem;

  // font weights
  --thin: 250;
  --regular: 500;
  --semi-bold: 650;
  --bold: 800;

  // text line spacing
  --spacing: 2;
  --compact: 1.5;

  // effects
  --rounded: 3px;
  --shadow: 0 0 5px 0 var(--overlay);
  --transition: 0.2s ease;
}
